import { Component } from '@angular/core';
import { ActionSheetController  ,AlertController, IonicPage,ModalController, NavController, NavParams, InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';
import { pdfx } from '../../../providers/pdf/pdfx';

@IonicPage()
@Component({
  selector: 'page-convenio-calusacd2',
  templateUrl: 'convenio-calusacd2.html',
})
export class ConvenioCalusacd2Page {

  public loading: boolean = true;
  items: any=[];
  items2: any=[];
  grupo:any;
    generated = '';

    total:any;
    public hasFilter: boolean = false;

  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  username: string = "";
  idempresa:any;
  idafiliado:any='na';
//private barcodeScanner:BarcodeScanner,
  constructor( public Pdfx:  pdfx,public actionSheetCtrl: ActionSheetController,public alertCtrl: AlertController,public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService
    ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth

    ) {
      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value

    })


  }



  public clearSearchg(): void {
    this.gSearchQuery = '';
  }
  showFilter()
  {




  }



  pideregistros() {
      let self = this;
      self.loading = true;
      this.regService.getReg(this.navParams.data.idempresa + '/1' +'/todos','conveniocalusacd2s').then((data) => {
        this.items=data;
        this.items2=data;
     //   this.total=(this.navParams.data.record.monto)*this.items.length
        self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
   // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })
  });
  }
  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
  }
  ionViewDidLoad() {




    this.pideregistros();
  }

  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }






  asigna2(item : any, no) : void
  {


    this.regService.getReg2b(item.idasigna+'/' +'1/todosautorizaxid','asignacalusacs').then((data) => {
      var  aa:any=[];
      aa=data
      var bb:any=[]

      bb=aa.tt2


         this.Pdfx.daordenpago2(aa.tt._id,'',aa.tt,bb)



     }, (err) => {

       this.msj.showError3('Error',err.error);
     });

  }


  deleteRecord(item : any, no) : void
  {

    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operación?',
    buttons: [
      {text: 'Si',
        handler: () => {

          let recordID 		: string		= item._id;
          this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'conveniocalusacd2s').then((data) => {
             this.pideregistros()
            }, (err) => {
              this.msj.showError3('Error',err.error);
          });

        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();




  }
  value:any;

  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('ConvenioCalusacd2CreatePage', { record : item,op:'',idgrupo:'',idempresa:this.navParams.data.idempresa },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.pideregistros()
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
   addRecord() : void
   {
    let addModal = this._MODAL .create('ConvenioCalusacd2CreatePage',{ record : null,op:'',idgrupo:'',idempresa:this.navParams.data.idempresa  },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.pideregistros()
      }
    })
    addModal.present();

      //this.navCtrl.push('EmpresaCreatePage');
   }

  random(): number {
    let rand = Math.floor(Math.random()*20)+1;
    return rand;
 }

    private davariable(cad:string,varx:string):string {
    var re="";
     var otro=cad
     var bb:any=[]
     if(otro.indexOf(varx)>0)
     {
     bb=otro.split(varx)


     if(bb.length==0)
       {
         re="error"
       }
       else{

         var cc:any=bb[1].split(';')
         var dd:any=cc[1]
         var ee:any=dd.split('&')
         re=ee[0];

       }
      }
      else
      {
        re="error"
      }

     return re;
   }
   public fechax                 : any;

  genera(item : any, no) : void
  {





    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operación?',
    buttons: [
      {text: 'Si',
        handler: () => {


          var cuitt='';
          cuitt=String(this.random());
        this.regService.displayPreloader('Espere un momento...')
          //this.authService.getReg(ident+ '/'+this.nombre1+'/' +montot + '/29/2020/17/1','siifs').then((result) =>
          this.regService.getReg2b(cuitt+ '/'+'Orden de pago especial'+'/' +item.monto +".00" + '/30/2021/' +'152'+ '/1','siifs').then((data) => {

              this.items=data;

              var aa:any=[]
              aa=data;
              var otro=aa.cadena

              var gorden=this.davariable(otro,'orden_pago')
              var ordenpago=gorden
              var llave=this.davariable(otro,'llave')
              var fechax=this.davariable(otro,'fecha')


         if(gorden=="error")
         {
           this.msj.showError3('Error','No se pudo generar orden de pago, intente mas tarde');
              this.regService.hidePreloader();
         //  this.authService.hidePreloader();

           return;
         }



 this.regService.hidePreloader();

                  let   options       : any	     = {

                  operacion:'ordenpago',
                  noboletapago:ordenpago,
                  fechasiif:fechax,
                  llave:llave,
                  identificador:cuitt

                  };

                  this.regService.createReg2b(item._id,options,'conveniocalusacd2s').then((result) => {

                  this.pideregistros()


                  });




          }, (err) => {
            this.msj.showError3('Error','Existe un problema al generar la boleta de pago, comuniquese con unidad administrativa CALUSAC');
   this.regService.hidePreloader();
          });




        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();





  }


   imprime(item : any, no) : void
 {


 this.Pdfx.daordenpago2acalusac('ORDEN_PAGO_CALUSAC',item.noboletapago,'Orden de pago especiales',(item.monto),'CALUSAC',item.identificador,'152',item.llave,'Escuela de Ciencias Lingüísticas',item.fechasiif,'21' ,'N/A','Orden de Pago especial ')

 }


 imprime2(item : any, no) : void
 {


this.regService.displayPreloader('Espere un momento...')
this.regService.getReg2b(item.identificador+ '/'+item.noboletapago +'/busca2','siifs').then((result) => {
  var aa:any=[];
  aa=result;
  var otro=aa.cadena
  if(otro=='Orden pago ya ha sido utilizada en el sistema.')
  {
    this.regService.hidePreloader();
    this.msj.showError3('Información',otro);

    return;
  }

  this.fechax=this.davariable(otro,'fecha')

 var cctemp =otro.indexOf('Boleta no pagada')
 if(cctemp>0)
 {
  this.regService.hidePreloader();
  this.msj.showError3('Información','Por favor realice su pago, para poder continuar con su asignación.');


 }
 else{

 if(aa.cadena=='<?xml version="1.0" encoding="ISO-8859-1"?><SOAP-ENV:Envelope SOAP-ENV:encodingStyle="http://schemas.xmlsoap.org/soap/encoding/" xmlns:SOAP-ENV="http://schemas.xmlsoap.org/soap/envelope/" xmlns:xsd="http://www.w3.org/2001/XMLSchema" xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns:SOAP-ENC="http://schemas.xmlsoap.org/soap/encoding/"><SOAP-ENV:Body><ns1:getDataResponse xmlns:ns1="urn:miserviciowsdl"><return xsi:type="xsd:string"></return></ns1:getDataResponse></SOAP-ENV:Body></SOAP-ENV:Envelope>')
{
  cctemp=1
 // this.msj.showError3('Información','Por favor realice su pago, para poder continuar con su asignación.');

}
else{

  cctemp=-1
}

  if(cctemp==-1)
  {



    this.regService.hidePreloader()
//actualiza estado



let   options       : any	     = {
  operacion:'actualizarinfor',
 estado : 'pagada'
};



   if(options){
      this.regService.createReg(item._id,options,'conveniocalusacd2s').then((result) => {
        this.pideregistros()

        this.msj.showError3('Mensaje','Orden de pago fue pagada con exito, por favor realice asignación de estudiantes');



     }, (err) => {
      this.regService.hidePreloader();
      this.msj.showError3('Error',err.error);
     });
  }




}
else
{
  this.regService.hidePreloader();
  this.msj.showError3('Información','Por favor realice su pago, para poder continuar con su asignación.');



}
 }



});

 }






}



